<template>
  <!-- 我的计划 -->
  <div class="my-plan">
    <template v-if="planInfo.length">
      <h3 class="title">培训计划</h3>
      <template v-for="(item, index) of planInfo">
        <div :key="index" class="stage">
          <div class="image-stage">
            <img
              src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217174139733/1581932499395.png"
              alt="阶段"
            />
            <span class="stage-text">{{ item.title }}</span>
          </div>
          <div class="plan-info">
            <p class="stage-title">{{ item.ptitle }}</p>
            <template v-for="second of item.children">
              <p :key="second.id" class="first-menu">{{ second.ptitle }}</p>
              <p
                v-for="third of second.children"
                :key="third.id"
                class="second-menu"
              >
                {{ third.ptitle }}
              </p>
            </template>
          </div>
        </div>
      </template>
    </template>
    <no-jurisdiction v-if="!planInfo.length" title="你还没有领取计划" />
  </div>
</template>

<script>
import { myPlan } from '@/api/study'
import NoJurisdiction from './NoJurisdiction'
export default {
  components: {
    NoJurisdiction
  },
  data() {
    return {
      planInfo: ''
    }
  },
  created() {
    this.getPlan()
  },
  methods: {
    async getPlan() {
      const { data, code } = await myPlan()
      if (code === 20000 && data.result) this.planInfo = data.list
    }
  }
}
</script>

<style scoped lang="scss">
.my-plan {
  margin: 20px;
  height: 100%;
  font-size: 18px;
  text-align: center;
  color: #17233d;
  .stage {
    display: flex;
    margin: 0 auto 20px;
    width: 1200px;
    min-height: 204px;
    border-radius: 4px;
    box-shadow: 0 0 4px 2px #f0f0f0;
    .image-stage {
      padding: 45px 50px;
      width: 170px;
      text-align: center;
      background: rgba(246, 247, 251, 0.64);
      img {
        width: 65px;
        height: 120px;
      }
      .stage-text {
        position: relative;
        top: -20px;
        color: #fff;
        .plan-info {
          padding: 20px;
          p {
            font-size: 16px;
            color: #576277;
            margin-bottom: 16px;
            .first-menu {
              margin-left: 20px;
            }
            .second-menu {
              margin-left: 33px;
            }
          }
        }
      }
    }
  }
}
</style>
